* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   text-decoration: none;
   border: none;
   outline: none;
   scroll-behavior: smooth;
   font-family: 'poppins', sans-serif;
}

:root {
   --bg-color: #1f242d;
   --second-bg-color: #323946;
   --taxt-color: #fff;
   --main-color: #0ef;

}

html {
   font-size: 62.5%;
   overflow-x: hidden;

}

body {
   background: var(--bg-color);
   color: var(--taxt-color);

}

section {
   min-height: 100vh;
   padding: 10rem 9% 2rem;
}
.navbar-mob{
   position: absolute;
   margin: 7px 9px 7px 7px;
   border-radius: 5px;
   top: 100%;
   left: 0;
   width: 100%;
   padding: 1rem 3%;
   background: var(--bg-color);
   border-top: .1rem solid rgba(0, 0, 0, .2);
   box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .2);
   display: none;
}

.header {
   position: fixed;
   top: 0;
   left: 0;
   width: 99%;
   padding: 2rem 9%;
   background: var(--bg-color);
   display: flex;
   justify-content: space-between;
   align-items: center;
   z-index: 100;
   margin: 10px 20px 10px 3px;
   border-radius: 20px;
   box-shadow: 2px 2px 5px 5px rgb(82, 80, 78);
}

.header.stickey {
   border-bottom: .1rem solid rgba(0, 0, 0, .2);
}

.logo {
   font-size: 2.5rem;
   color: var(--taxt-color);
   cursor: default;
}


.navbar a {
   font-size: 1.7rem;
   color: var(--taxt-color);
   margin-left: 4rem;
   cursor: pointer;
}

.navbar a:hover,
.navbar a.active {
   color: var(--main-color);

}

#menu-icon {
   font-size: 3.6rem;
   color: var(--taxt-color);
   display: none;

}

.home {
   padding-top: 100px;
   display: flex;
   justify-content: center;
   align-items: center;
}

.home-img img {
   width: 35vw;
   animation: Floatimage 4s ease-in-out infinite;

}

.home-content h3 {
   font-size: 3.2rem;
   font-weight: 700;
}

.home-content h3:nth-of-type(2) {
   margin-bottom: 2rem;

}

span {
   color: var(--main-color);

}

.home-content h1 {
   font-size: 5.6rem;
   font-weight: 700;
   line-height: 1.3;
}

.home-content p {
   font-size: 1.6rem;
   word-spacing: 2%;
}

.social-media a {
   display: inline-flex;
   justify-content: center;
   align-items: center;
   height: 4rem;
   width: 4rem;
   background: transparent;
   border: .2rem solid var(--main-color);
   border-radius: 50%;
   font-size: 2rem;
   color: var(--main-color);
   margin: 3rem 1.5rem 3rem 0;
   transition: 0.5s ease;
}

.social-media a:hover {
   background: var(--main-color);
   color: var(--second-bg-color);
   box-shadow: 0 0 1rem var(--main-color);
}

.btn {
   display: inline-block;
   padding: 1rem 2.8rem;
   background: var(--main-color);
   border-radius: 4rem;
   box-shadow: 0 0 1rem var(--main-color);
   color: var(--second-bg-color);
   letter-spacing: .1rem;
   font-weight: 600;
   transition: .5s ease;
}

.btn:hover {
   box-shadow: none;
}

.about {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 2rem;
   background: var(--second-bg-color);
   border-radius: 20px;
}

.ultag {
   margin-left: 30px;
   font-size: 22px;
   color: yellowgreen;
}

.about-img img {
   width: 35vw;
   animation: Floatimage 4s ease-in-out infinite;
}

@keyframes Floatimage {

   0% {
      transform: translateY(0);
   }

   50% {
      transform: translateY(-2.4rem);
   }

   100% {
      transform: translateY(0);
   }
}

.heading {
   text-align: center;
   font-size: 4.5rem;
}

.about-content h2 {
   color: var(--taxt-color);
   text-align: left;
   line-height: 1.2;
}

.about-content h3 {
   font-size: 2.6rem;
}

.about-content p {
   font-size: 1.6rem;
   margin: 2rem 0 3rem;
}

.service h2 {
   color: var(--taxt-color);
   margin-bottom: 5rem;

}

.services-container {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
   gap: 2rem;

}

.services-container .services-box {
   flex: 1 1 30 rem;
   background: var(--second-bg-color);
   padding: 3rem 2rem 4rem;
   border-radius: 2rem;
   text-align: center;
   border: .2rem solid var(--bg-color);
   transition: .5s ease;
}

.services-container .services-box:hover {
   border-color: var(--main-color);
   transform: scale(1.02);
}

.services-box i {
   font-size: 7rem;
   color: var(--main-color);
}

.services-box h3 {
   font-size: 2.6rem;
}

.services-box p {
   font-size: 2.6rem;
   margin: 1rem 0 3rem;
}

.portfolio {

   background: var(--second-bg-color);
   border-radius: 20px;
}

.portfolio h2 {
   color: var(--taxt-color);
   margin-bottom: 4rem;
}

.portfilio-container {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   align-items: center;
   gap: 2.5rem;
}

.portfilio-container .portfilio-box {
   position: relative;
   border-radius: 2rem;
   box-shadow: 0 0 1rem var(--bg-color);
   overflow: hidden;
   display: flex;
}

.portfilio-box img {
   width: 100%;
   transition: .5, ease;

}

.portfilio-box:hover img {
   transform: scale(1.1);
}

.portfilio-box .portfolio-layer {
   position: absolute;
   bottom: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background: linear-gradient(rgba(0, 0, 0, .1), var(--main-color));
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
   text-align: center;
   padding-inline-start: 0 4rem;
   transform: translateY(100%);
   transition: .5, ease;
}

.portfilio-box:hover .portfolio-layer {

   transform: translateY(0);
}

.portfolio-layer h4 {
   font-size: 3rem;
}

.portfolio-layer p {
   font-size: 1.6rem;
   margin: .3rem 0 1rem;
}

.portfolio-layer a {
   display: inline-flex;
   justify-content: center;
   align-items: center;
   width: 5rem;
   height: 5rem;
   background: var(--taxt-color);
   border-radius: 50%;
}

.portfolio-layer a:hover {
   border: 2px red solid;
}

.portfolio-layer a i {

   font-size: 2rem;
   color: var(--second-bg-color);
}


.Contact h2 {
   margin-bottom: 3rem;
   color: var(--taxt-color);
}

.Contact form {
   max-width: 70rem;
   margin: 1rem auto;
   text-align: center;
   margin-bottom: 3rem;
}

.Contact form .input-box {
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
}

.Contact form .input-box input,
.Contact form textarea {
   width: 100%;
   padding: 1.5rem;
   font-size: 1.6rem;
   color: var(--taxt-color);
   background: var(--second-bg-color);
   border-radius: .8rem;
   margin: .7rem 0;
}

.Contact form .input-box input {
   width: 49%;
}

.Contact form textarea {
   resize: none;
}

.Contact form .btn {
   margin-top: 2rem;
   cursor: pointer;
}

.footer {
   display: flex;
   justify-content: space-between;
   align-items: center;
   flex-wrap: wrap;
   padding: 2rem 9%;
   background: var(--second-bg-color);
   border-radius: 20px;

}

.footer-text p {
   font-size: 1.6 rem;

}

.footer-iconTop a {
   display: inline-flex;
   justify-content: center;
   align-items: center;
   padding: .8rem;
   background: var(--main-color);
   border-radius: .8rem;
   transition: .5s ease;
   cursor: pointer;
}

.footer-iconTop a:hover {
   box-shadow: 0 0 1rem var(--main-color);
}

.footer-iconTop a i {
   font-size: 2.4rem;
   color: var(--second-bg-color);
}

/* BREAKPOINTS*/
@media (max-width: 1200px) {
   html {
      font-size: 55%;
   }
}

@media (max-width: 991px) {
   .header {
      padding: 2rem 3%;
   }

   .section {
      padding: 10rem 3% 2rem;
      overflow: hidden;
   }

   .service {
      padding-bottom: 7rem;
   }

   .portfolio {
      padding-bottom: 7rem;
   }

   .Contact {
      min-height: auto;
   }

   .footer {
      padding: 2rem 3%;
   }
}

@media (max-width: 768px) {
   #menu-icon {
      display: block;
   }

   .navbar{
      position: absolute;
      margin: 7px 9px 7px 7px;
      border-radius: 5px;
      top: 100%;
      left: 0;
      width: 100%;
      padding: 1rem 3%;
      background: var(--bg-color);
      border-top: .1rem solid rgba(0, 0, 0, .2);
      box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .2);
      display: block;
   }

   .navbar.active {
      display: none;
   }

   .navbar a {
      display: block;
      font-size: 2rem;
      margin: 3rem 0;

   }

   .home {
      flex-direction: column-reverse;
      margin: auto;
   }

   .home-content h3 {
      font-size: 2.6rem;
   }

   .home-content h1 {
      font-size: 5rem;
   }

   .home-img img {
      width: 70vw;
      margin-top: 4rem;
   }

   .about {
      flex-direction: column;
   }

   .about img {
      width: 70vw;
      margin-top: 4rem;
   }

   .service h2 {
      margin-top: 3rem;
   }

   .portfolio {
      margin-bottom: 3rem;
   }

   .portfilio-container {
      grid-template-columns: repeat(2, 1fr);
   }
}

@media (max-width: 617px) {
   .portfilio-container {
      grid-template-columns: 1fr;
   }

}

@media(max-width: 450px) {
   html {
      font-size: 50%;
   }

   .Contact form .input-box input {
      width: 100%;
   }

}

@media(max-width: 365px) {
   .home-img img {
      width: 90vw;
   }

   .about-img img {
      width: 90vw;
   }

   .footer {
      flex-direction: column-reverse;
   }

   .footer p {

      text-align: center;
      margin-top: 2rem;
   }

   ;
}